@mixin position-size-color {
  text-align: center;
  transform: translateX(-50%);
  text-transform: uppercase;
  background-image: linear-gradient(
                  -225deg,
                  #231557 0%,
                  #44107a 29%,
                  #ff1361 67%,
                  #fff800 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}



.Home {
  //position: relative;
  max-width: 100vw;
  width: 100vw;
  color: #ffffff;
  height: 600px;

  //&_Section {
    &_Name {
      @include position-size-color;
      font-size: 100px;
      position: absolute;
      font-weight: 600;
      top: 40%;
      left: 50%;
      animation: slideName 3s linear, textclip 6s linear infinite;
      width: 600px;
    }

    &_Title {
      @include position-size-color;
      font-size: 60px;
      position: absolute;
      top: 60%;
      left: 50%;
      width: 800px;
      animation: slideTitle 7.5s linear, textclip 6s linear infinite;
    }
  //}
}

@keyframes slideName {
  0% {
    left: 100%;
    opacity: 0;
  }
  100% {
    left: 50%;
    opacity: 1;
  }
}

@keyframes slideTitle {
  0% {
    left: 150%;
    opacity: 0;
  }
  50% {
    left: 150%;
    opacity: 0;
  }
  100% {
    left: 50%;
    opacity: 1;
  }
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}



